#contact{
    display: grid;
    align-items: center;
    justify-content: center;

    grid-template-columns: 1fr 3fr 1fr;
}

#contact form{
    display: flex;
    flex-direction: column;
    width: 100%;
    grid-column: 2/3;
}
#contact form > label{
    color: var(--white);
    font-weight: 700;
    font-size: 2rem;
    margin-bottom: 2rem;
}
#contact form input[type="email"],
#contact form input[type="text"]{
    height: 3rem;
    background-color: var(--gray);
    outline: none;
    border: none;
    padding: .5rem;
    margin: .5rem 0;

    color: var(--white);
}

#contact form textarea{
    color: var(--white);
    height: 8rem;
    background-color: var(--gray);
    outline: none;
    border: none;
    padding: .5rem;
    margin: 1rem 0;
}

#contact form > div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
#contact form > div input{
    display: none;
}
#contact form > div label{
    height: 2rem;
    width: 8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--gray);
    color: var(--light-gray);

    font-weight: 700;

    cursor: pointer;
}
#contact form button{
    outline: none;
    border: none;
    height: 2rem;
    width: 8rem;
    position: relative;

    cursor: pointer;

    margin-top: -2%;

    background-color: transparent;
}
#contact form button div{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    position: absolute;
}
#contact form button div:first-of-type{
    
    border: 1px solid var(--white);
    margin-left: -5%;
    margin-top: -5%;
    z-index: 1;
}
#contact form button div:last-of-type{
    background-color: var(--darker-secondary-color);
    z-index: 200;
    color: var(--white);
    font-weight: 700;
}
::placeholder{
    font-weight: 700;
    color: var(--light-gray);
}

@media screen and (max-width: 600px) {
    #contact{
        display: flex;
    }
}